                </label>
                <label v-if="martial_status === 'M'" for="spouse_donation"><span class="label_text">{{ lang.spouse }}</span>
                <input id="spouse_donation" type="number" min="0" max="999999999" v-model="spsDona" />
                </label>
            </div>

