                    <label for="slfMedInsu_ppl"><span class="label_text lbl_large"></span><span class="icon_info" v-tooltip.bottom.end="{ html: 'medic_insu_info' }">&nbsp;</span>
                    <quantity v-bind:min="s_MedicInsu_min" v-bind:max="s_MedicInsu_max" v-model="slfMedInsu_ppl" v-on:drag-end=""></quantity>
