                <div class="cont">
                    <label for="NBbb"><span class="label_text lbl_large"></span><!-- <input id="NBbb" type="text" v-model="NBbb" v-on:change="D3aOnChange()" /> <!- -  STCIn21 -->
                    <quantity v-bind:min="s_bb_min" v-bind:max="s_bb_max" v-model="NBbb" v-on:drag-end="D3aOnChange()"></quantity>
                    </label>
                    <label v-if="NBbb > 0" for="NBbb_DIS"><span class="label_text"></span><!--<input id="NBbb_DIS" type="text" v-model="NBbb_DIS" v-on:change="D4aOnChange()" /> <!- -  STCIn10 -->
                    <quantity v-bind:min="s_bb_min" v-bind:max="NBbb" v-model="NBbb_DIS" v-on:drag-end="D4aOnChange()"></quantity>
                    </label>

                    <label for="CAbb"><span class="label_text lbl_large"></span><!-- <input id="CAbb" type="text" v-model="CAbb" v-on:input="D3OnChange()" /> <!- -   STCIn4 -->
                    <quantity v-bind:min="s_bb_min" v-bind:max="9 - NBbb" v-model="CAbb" v-on:drag-end="D3OnChange()"></quantity>
                    </label>
                    <label v-if="CAbb > 0" for="CAbb_DIS"><span class="label_text"></span><!--<input id="CAbb_DIS" type="text" v-model="CAbb_DIS" v-on:change="D4OnChange()" /> <!- -  STCIn22 -->
                    <quantity v-bind:min="s_bb_min" v-bind:max="CAbb" v-model="CAbb_DIS" v-on:drag-end="D4OnChange()"></quantity>
                    </label>

