                            <span class="yn"></span>
                            <switches v-model="single_parent" theme="bootstrap" color="primary"></switches>
                            <span class="yn"></span>
                            <!-- <input id="single_parent" type="number" v-model="single_parent" /> <!- -  STCIn23 -->
                        </div>
                    </label>
                </div>
            </div>


