                    -->

                    <label v-if="brosis_dep > 0" for="brosis_dep_DIS"><span class="label_text"></span><!--<input id="brosis_dep_DIS" type="text" v-model="brosis_dep_DIS" v-on:change="D6OnChange()" /> <!- -  STCIn11 -->
                    <quantity v-bind:min="s_bb_min" v-bind:max="brosis_dep" v-model="brosis_dep_DIS" v-on:drag-end="D6OnChange()"></quantity>
