                    <label for="resi_parent_5560"><span class="label_text lbl_large"></span><span class="icon_info" v-tooltip.bottom.end="{ html: 'parent_5560_info' }">&nbsp;</span><!-- <input id="resi_parent_5560" type="text" v-model="resi_parent_5560" /> <!- -  STCIn17 -->
                    <quantity v-bind:min="s_Elder_min" v-bind:max="s_Elder_max" v-model="resi_parent_5560" v-on:drag-end=""></quantity>
